.rt-fix-tools {
    width: 4.5rem;
    border: #EFF3F5 1px solid;
    background-color: #fff;
    color: #798B8B;
    cursor: pointer;
    border-radius: 4px;
    box-shadow: 2px 2px 4px 0 rgba(35, 49, 56, .08), 4px 4px 8px 0 rgba(35, 49, 56, .1), 6px 6px 12px 0 rgba(35, 49, 56, .12);
    opacity: .86;
    font-size: .75rem;
    z-index: 99;
}

.rt-fix-tools.df-tools--intro,
.rt-fix-tools.df-tool {
    padding: 1rem .25rem .75rem;
    position: fixed;
    right: 24px;
    animation: zoomIn 1s ease-in-out;
}

.rt-fix-tools.df-tool {
    bottom: 144px;
}

.rt-fix-tools.df-tools--intro {
    bottom: 232px;
}

.rt-fix-tools > i {
    display: block;
}

.rt-fix-tools.df-tools--intro>i,
.rt-fix-tools.df-tool>i {
    font-size: 1.75rem;
    color: #52B8C5;
    margin-bottom: .375rem;
}

.rt-fix-tools:hover {
    box-shadow: 2px 2px 6px 0 rgba(35, 49, 56, .12), 4px 4px 10px 0 rgba(35, 49, 56, .16), 6px 6px 14px 0 rgba(35, 49, 56, .18);
    opacity: 1;
    transition: all .25s ease-in-out;
}

.go-to-top {
    display: none;
    padding: .5rem .25rem .375rem;
}

.go-to-top>i {
    font-size: 1.75rem;
    margin-bottom: .25rem;
}

.go-to-top>font {
    font-size: .75rem;
}

.go-to-top:hover,
.go-to-top:focus:hover {
    color: #52B8C5;
}
