.mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.mask.mask_white {
    background-color: rgba(255, 255, 255, .24);
}

.mask.mask_dark {
    background-color: rgba(0, 0, 0, .16);
}

.mask.mask_jobbank {
    background-position: center;
    background-size: 12rem
}

.mask.mask_square.mask_square {
    background-position: center top;
    background: url("data:image/svg+xml;base64,") repeat 0 0;
    background-size: 2rem 2rem;
    opacity: .72
}

.mask.mask_pattern {
    background: url('../img/pattern.png');
}

.mask.mask_lt-oval {
    background: url('../img/lt-oval.png') no-repeat;
    background-position: left bottom;
}

.mask.mask_rt-oval {
    background: url('../img/rt-oval.png') no-repeat;
    background-position: right top;
}

.mask.mask_group {
    background: url('../img/mask-group.png') no-repeat;
    background-size: cover;
    background-position: center;
}